import { Injectable } from '@angular/core';
import { ActivatedRoute, NavigationExtras, Router } from '@angular/router';

@Injectable()
export class ManageUpcomingAndHistoricalPaymentsNavigationService {
  constructor(private readonly router: Router, private readonly route: ActivatedRoute) {}
  /**
   * Navigation to history payments view tab
   */
  navigateToHistory(extras?: NavigationExtras): void {
    this.router.navigate(['history'], { relativeTo: this.route, ...(extras || {}) });
  }
  /**
   * Navigation to upcoming payments view tab
   */
  navigateToUpcoming(extras?: NavigationExtras): void {
    this.router.navigate(['upcoming'], { relativeTo: this.route, ...(extras || {}) });
  }
  /**
   * Method to return correct tab index in order to activate it
   */
  mapRouteToTabIndex(): 1 | 0 {
    const routeSections = this.router.url.split('/');
    return routeSections[routeSections.length - 1] === 'history' ? 1 : 0;
  }
}
